<style type="text/css">
  .coupon-info-form {
    width: 350px;
    height: 680px;
    margin: auto;
    text-align: left;
  }

  .coupon-info-form .el-form-item__label {
    width: 110px;
  }

  .coupon-info-input {
    width: 230px;
  }

  .remark{
    width: 220px;
    font-size: 12px;
    color: #cdcdcd;
    line-height: normal;
  }
</style>
<template>
  <el-container>
    <el-main v-loading="loading" element-loading-text="" style="background-color: #FFFFFF;">
      <div style="width: 100%;float: left;clear: left;margin: auto;">
        <el-form :inline="true" :model="coupon" :rules="rules" ref="coupon" class="demo-form-inline coupon-info-form">
          <el-form-item label="" prop="couponType">
            <el-select v-model="coupon.couponType" placeholder=""
                       class="coupon-info-input" @change="chooseCouponType()">
              <el-option label="" value=''></el-option>
              <el-option label="" value='1'></el-option>
              <el-option label="" value='2'></el-option>
              <!--              <el-option label="" value='3'></el-option>-->
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="couponName">
            <el-input v-model="coupon.couponName" maxLength="20" placeholder=""
                      class="coupon-info-input"></el-input>
          </el-form-item>
          <el-form-item label="" prop="denomination">
            <el-input v-model.number="coupon.denomination" min="1" max="99999" placeholder=""
                      class="coupon-info-input"></el-input>
          </el-form-item>
          <el-form-item label="" prop="gainAppletId">
            <el-select v-model="coupon.gainAppletId" placeholder="" class="coupon-info-input">
              <el-option label="" value=''></el-option>
              <el-option v-for="(item, index) in appletList" :key="index" :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="gainPrice" v-if="coupon.couponType !== '1'">
            <el-input v-model.number="coupon.gainPrice" placeholder="10210"
                      class="coupon-info-input"></el-input>
          </el-form-item>
          <el-form-item label="" prop="useAppletId">
            <el-select v-model="coupon.useAppletId" placeholder="" class="coupon-info-input">
              <el-option label="" value=''></el-option>
              <el-option v-for="(item, index) in appletList" :key="index" :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="usePrice">
<!--            <el-input v-model.number="coupon.usePrice" placeholder=""-->
<!--                      class="coupon-info-input" readonly="readonly" v-if="coupon.couponType === '1'"></el-input>-->
            <el-input v-model.number="coupon.usePrice" placeholder=""
                      class="coupon-info-input"></el-input>
          </el-form-item>
          <el-form-item label="" prop="makeIssueNum">
            <el-input v-model.number="coupon.makeIssueNum" min="1" max="100" placeholder=""
                      class="coupon-info-input"></el-input>
            <br>
            <div class="remark">0()</div>
          </el-form-item>
          <el-form-item label="" prop="activityStart">
            <el-date-picker type="datetime" placeholder="" v-model="coupon.activityStart"
                            :format="format" :value-format="valueFormat" :picker-options="pickerOptionsStart"
                            style="width: 100%;"></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="activityOver">
            <el-date-picker type="datetime" placeholder="" v-model="coupon.activityOver"
                            :format="format" :value-format="valueFormat" :picker-options="pickerOptionsStart"
                            style="width: 100%;"></el-date-picker>
          </el-form-item>
          <el-form-item label=" ">
            <el-button type="success" @click="onSubmit()" class="coupon-info-input" style="text-align: center;">
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>
<script type="text/javascript">
    import {Loading} from 'element-ui'

    export default {
        name: 'coupon-create',
        data () {
            return {
                loading: false,
                format: 'yyyyMMdd',
                valueFormat: 'yyyy/MM/dd',
                appletList: [],
                coupon: {
                    couponType: '',
                    couponName: '',
                    denomination: '',
                    gainAppletId: '',
                    gainPrice: '',
                    useAppletId: '',
                    usePrice: '',
                    makeIssueNum: '',
                    activityStart: '',
                    activityOver: ''
                },
                rules: {
                    couponType: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    couponName: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'string', min: 1, max: 20, message: '1-20', trigger: 'blur'}
                    ],
                    denomination: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', min: 1, max: 99999, message: '1-99999', trigger: 'blur'}
                    ],
                    gainAppletId: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gainPrice: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', min: 0, max: 99999, message: '0-99999', trigger: 'blur'}
                    ],
                    useAppletId: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    usePrice: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', min: 0, max: 99999, message: '0-99999', trigger: 'blur'}
                    ],
                    makeIssueNum: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', min: 0, max: 99999, message: '0-99999', trigger: 'blur'}
                    ],
                    activityStart: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    activityOver: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                pickerOptionsStart: {
                    disabledDate (time) {
                        return time.getTime() < Date.now() - 8.64e7
                    }
                }
            }
        },
        created () {
            this.loadAddCreateCoupon()
        },
        mounted () {

        },
        methods: {
            loadAddCreateCoupon () {
                this.loading = true
                this.$axios({
                    url: '/api/user/coupon/loadAddCouponInfo',
                    method: 'post'
                }).then(res => {
                    if (res.data.code === '1') {
                        this.appletList = res.data.data
                    }
                    this.$global.exitLoad(this, null, res.data)
                }).catch(error => {
                    console.info('', error)
                    this.$global.exitLoad(this, null, '')
                })
            },
            onSubmit () {
                this.$refs['coupon'].validate((valid) => {
                    if (valid) {
                        let loading = Loading.service({fullscreen: true, text: ''})
                        this.$axios({
                            url: '/api/user/coupon/addCouponInfo',
                            method: 'post',
                            data: this.coupon
                        }).then(res => {
                            console.info('', res.data)
                            let that = this
                            res.data.code === '1' ? this.$message.success({
                                message: res.data.data,
                                duration: 1000,
                                onClose: function () {
                                    that.$emit('refreshList')
                                }
                            }) : this.$message.error(res.data.data)
                            this.$global.exitLoad(this, loading, res.data)
                        }).catch(error => {
                            console.info('', error)
                            this.$global.exitLoad(this, loading, '')
                        })
                    }
                })
            },
            chooseCouponType () {
                let couponType = this.coupon.couponType
                if (couponType === '1') {
                    // this.coupon.usePrice = 0
                    this.coupon.gainPrice = 0
                } else {
                    this.coupon.usePrice = ''
                    this.coupon.gainPrice = ''
                }
            }
        }
    }
</script>



